:root {
  --font-body: "Outfit", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --softblue: hsl(215, 51%, 70%);
  --cyan: hsl(178, 100%, 50%);
  --verydarkbluembg: hsl(217, 54%, 11%);
  --verydarkbluecbg: hsl(216, 50%, 16%);
  --verydarkbluel: hsl(215, 32%, 27%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}
html:focus-within {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: var(--font-body);
  background-color: var(--verydarkbluembg);
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

ul,
ol {
  list-style: none;
}

.container {
  min-height: 100vh;
  max-width: 90rem;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer {
  font-size: 11px;
  text-align: center;
  color: var(--white);
}

footer a {
  color: var(--softblue);
}

.card-container {
  width: 350px;
  height: 600px;
  background-color: var(--verydarkbluecbg);
  padding: 1.5rem;
  margin: 0 1.5rem;
  border-radius: 15px;
}
.card-container .card-display {
  position: relative;
}
.card-container .card-display .nft {
  width: 300px;
  height: 300px;
  position: relative;
}
.card-container .card-display .nft img {
  border-radius: 10px;
  width: 100%;
  vertical-align: top;
}
.card-container .card-display .nft::after {
  content: url("../app/media/images/icon-view.svg");
  text-align: center;
  vertical-align: middle;
  padding: 8rem 0;
  position: absolute;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 255, 247, 0.6);
  opacity: 0;
  transition: all 250ms;
}
.card-container .card-display .nft:hover::after {
  opacity: 1;
  cursor: pointer;
}
.card-container .card-display h1 {
  font-weight: 700;
  font-size: 1.3rem;
  color: var(--white);
  margin: 1.5rem 0 1rem 0;
  transition: color 250ms ease;
}
.card-container .card-display h1:hover {
  color: var(--cyan);
  cursor: pointer;
}
.card-container .card-display p {
  font-weight: 300;
  font-size: 1.1rem;
  color: var(--softblue);
}
.card-container .card-stats {
  display: flex;
  align-items: center;
  margin: 1.2rem 0;
}
.card-container .card-stats .price {
  color: var(--cyan);
  padding: 0 7rem 0 0.5rem;
  font-weight: 700;
  font-size: 0.9rem;
}
.card-container .card-stats .time {
  color: var(--softblue);
  padding-left: 0.3rem;
}
.card-container .divider {
  border-bottom: 1px solid var(--verydarkbluel);
  margin-bottom: 1rem;
  width: 100%;
  height: 1px;
  display: block;
}
.card-container .card-user {
  display: flex;
  align-items: center;
}
.card-container .card-user img {
  width: 10%;
  outline: 1px solid var(--white);
  border-radius: 50%;
}
.card-container .card-user p {
  color: var(--softblue);
  padding-left: 1rem;
}
.card-container .card-user .name {
  color: var(--white);
}
.card-container .card-user .name:hover {
  color: var(--cyan);
  cursor: pointer;
  transition: color 250ms ease;
}